import React, {
  Component,
  useContext,
  useState,
  useEffect,
  useRef,
} from "react";
import { UserContext } from "./functions/UserProvider.js";
import { signOut, signInWithGoogle, firestore } from "./functions/Firebase.js";

import { Navbar, Nav } from "react-bootstrap";
import logo from "../img/logo_multifolio2Recurso 3.png";
import NombreFoto from "./NombreFoto";
import "./styles/navbar2.css";
import "./styles/scroll.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faBriefcase,
  faHome,
  faInfoCircle,
  faListAlt,
  faSearch,
  faSignInAlt,
  faSignOutAlt,
  faUser,
} from "@fortawesome/free-solid-svg-icons";
import { Link } from "react-router-dom";

const MyNavbar2 = (props) => {
  const { user } = useContext(UserContext);
  const isMountedRef = useRef(null);
  const [data, setData] = useState(null);
  const [loading, setLoading] = useState(true);
  const db = firestore;

  useEffect(() => {
    isMountedRef.current = true;
    if (user) {
      db.collection("users")
        .doc(user.uid)
        .get()
        .then((doc) => {
          if (doc && isMountedRef.current) {
            setData(doc.data());
            setLoading(false);
          }
        });
      //console.log(user.displayName);
    } else {
      if (data) {
        //setData(null);
      }
    }
    setLoading(false);
    return () => (isMountedRef.current = false);
  }, [user, db]);

  return (
    <>
      <Navbar
        collapseOnSelect={true}
        expand="lg"
        fixed="bottom"
        className="my-navbar p-0 col-lg-2 shadow-lg"
      >
        <div className="brandContainer text-center p-0">
          <Navbar.Brand className="mx-0" href="#inicio">
            <div className="text-center">
              <img
                className="multifolioLogo col-lg-8 mb-2"
                src={logo}
                alt="logo_multifolio"
                style={{ display: "inline-block" }}
              />
            </div>
            <h1 className="brandTitle">MULTIFOLIO</h1>
          </Navbar.Brand>
        </div>
        <Navbar.Toggle
          aria-controls="basic-navbar-nav"
          className="mr-3"
          style={{ borderColor: "#fff" }}
        />

        <Navbar.Collapse id="my-navbar col-6">
          <Nav className="flex-column ">
            {!loading && user ? (
              <div className="dropdown align-self-center mb-2">
                <button
                  className="btn btn-outline-secondary d-flex dropdown-toggle align-items-center p-2 text-center"
                  data-toggle="dropdown"
                  type="button"
                  style={{ lineHeight: "45px" }}
                >
                  <NombreFoto
                    name={data ? data.nickName : user.displayName}
                    photo={data ? data.photo : user.photoURL}
                  />
                </button>
                <div
                  className="dropdown-menu  position-absolute"
                  style={{ fontSize: "1.2em" }}
                >
                  <Link className="dropdown-item" to="/mi_perfil">
                    <FontAwesomeIcon icon={faUser} /> Mi perfil
                  </Link>
                  <Link className="dropdown-item" to="/mis_proyectos">
                    <FontAwesomeIcon icon={faBriefcase} /> Mis proyectos
                  </Link>
                  <Link to="/inicio">
                    <div
                      className="dropdown-item"
                      onClick={() => {
                        signOut();
                      }}
                      style={{ cursor: "pointer" }}
                    >
                      <FontAwesomeIcon icon={faSignOutAlt} /> Cerrar sesi√≥n
                    </div>
                  </Link>
                </div>
              </div>
            ) : loading ? (
              <div>LOADING...</div>
            ) : null}
            {user ? (
              <div></div>
            ) : (
              <button
                type="button"
                className="btn btn-success btn-lg my-2 mx-auto"
                onClick={signInWithGoogle}
                style={{ textTransform: "none" }}
              >
                <FontAwesomeIcon icon={faSignInAlt} /> Iniciar sesi√≥n
              </button>
            )}
            <Link className="nav-link" to="/inicio">
              <FontAwesomeIcon icon={faHome} /> Inicio
            </Link>
            <Link className="nav-link" to="/categorias">
              <FontAwesomeIcon icon={faListAlt} /> Categorias
            </Link>
            <Link className="nav-link" to="/buscar">
              <FontAwesomeIcon icon={faSearch} /> Buscar
            </Link>
            <Link className="nav-link" to="/acerca_de">
              <FontAwesomeIcon icon={faInfoCircle} /> Acerca de...
            </Link>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
    </>
  );
};

export default MyNavbar2;
